@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@250;300&display=swap);
.container {
    position: relative;
    margin-left: 0;
    padding-left: 0;
    max-width: 10px;
}

.navbar {
    background-color: #2a5657;
    text-align: left;
    width: 100%;
    position: -webkit-sticky;
    position: sticky;
    padding: 0 !important;
    transition: background-color 0.5s ease-in-out !important;
}

.navbar-nav .nav-link {
    color: #f5f5f5 !important;
    padding: 1rem 1rem !important;
    transition: color 0.5s ease-in-out !important;
}

.navbar-nav .nav-link:hover {
    color: #027498 !important;
}

.navbar-nav .nav-item a {
    position: relative;
    margin-right: 20px;
    transition: color 0.3s ease-in-out, -webkit-transform 0.5s ease-in-out !important;
    transition: transform 0.5s ease-in-out, color 0.3s ease-in-out !important;
    transition: transform 0.5s ease-in-out, color 0.3s ease-in-out, -webkit-transform 0.5s ease-in-out !important;
}

.navbar-nav .nav-item a:hover {
    background-color: transparent;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.toggle-icon {
    color: #f5f5f5 !important;
}

.navbar-toggler {
    padding: 0.5rem 1.5rem !important;
    position: relative !important;
    background-color: transparent !important;
    border: none !important;
    outline: 0 !important;
    font-size: 2rem !important;
    color: #f5f5f5 !important;
}
.navbar-toggler:focus,
.navbar-toggler:active {
    outline: 0 !important;
    box-shadow: none;
}

span {
    margin-left: 1rem;
    color: #f5f5f5;
}

#about {
    padding: 2rem 0;
}

.about-card {
    background-color: #263238;
    padding: 2rem;
    margin: 0 auto;
    max-width: 800px;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.8) !important;
    transition: 0.3s;
}

.about-card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.8) !important;
}

.card-header {
    display: flex;
    justify-content: center;
    padding-bottom: 1rem;
    background-color: #263238 !important;
}

.iconify {
    font-size: 2rem;
    margin: 0 1rem;
}

.square-icon {
    -webkit-animation: spin 2s linear infinite;
            animation: spin 2s linear infinite;
}

.triangle-icon {
    -webkit-animation: bounce 2s infinite;
            animation: bounce 2s infinite;
}

.circle-icon {
    -webkit-animation: pulse 2s infinite;
            animation: pulse 2s infinite;
}

.card-body {
    background-color: #ffffff;
    color: #263238;
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0px 10px 20px rgba(0,0,0,0.19), 0px 6px 6px rgba(0,0,0,0.23);
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 1.32em;
    line-height: 1.5;
    text-align: justify;
}

@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@-webkit-keyframes bounce {
    0%, 100% { -webkit-transform: translateY(0); transform: translateY(0); }
    50% { -webkit-transform: translateY(-20px); transform: translateY(-20px); }
}

@keyframes bounce {
    0%, 100% { -webkit-transform: translateY(0); transform: translateY(0); }
    50% { -webkit-transform: translateY(-20px); transform: translateY(-20px); }
}

@-webkit-keyframes pulse {
    0%, 100% { -webkit-transform: scale(1); transform: scale(1); }
    50% { -webkit-transform: scale(1.6); transform: scale(1.6); }
}

@keyframes pulse {
    0%, 100% { -webkit-transform: scale(1); transform: scale(1); }
    50% { -webkit-transform: scale(1.6); transform: scale(1.6); }
}
.tags {
    margin: 0.5rem 0 0.5rem 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: left;
}

.tag {
    font-size: 12px !important;
    text-align: center !important;
    padding: 5px 8px 5px 8px !important;
    vertical-align: baseline;
    font-weight: lighter !important;
    margin: 0 6px 6px 2px;
    letter-spacing: 1px;
    background-color: #2a5657;
    color: #f5f5f5;
    border-radius: 15px;
}

.title {
    font-size: 3em;
    text-align: center;
    color: #2a5657;
    margin-bottom: 2rem;
}

.vertical-timeline-element-title {
    text-align: center;
    margin-bottom: 0.7rem;
    font-size: 24px !important;
    color: #488e90;
}

.vertical-timeline-element-subtitle {
    text-align: center;
    margin-bottom: 0.7rem;
    font-size: 24px !important;
    color: #488e90;
}

.vertical-timeline-element-content {
    box-shadow: 0 0.25em 0.5em 0 rgba(0, 0, 0, 0.15), 0 0.4em 1.25em 0 rgba(0, 0, 0, 0.05) !important;
    padding: 2em 3em !important;
    background: #f5f5f5;
    border-radius: 10px;
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.vertical-timeline-element-content:hover {
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
}

.vertical-timeline-element-description{
    margin-bottom: 1rem;
    font-size: 16px !important;
    color: #333;
}

.vertical-timeline-element-icon {
    background: #8ecccb;
    color: #fff;
}

.vertical-timeline-element-content-arrow {
    border-right: 7px solid  #f5f5f5;
}

.vertical-timeline-element-date {
    color: #488e90;
    font-weight: bold !important;
    margin: 0 20px;
}

body {
    background: linear-gradient(135deg, #e6e6f0, #f5f5f5); /* Soft gradient to match the light theme */
    font-family: 'Roboto', sans-serif; /* Keeping it simple and professional */
}

.home-header {
    /* Using a dark overlay to ensure the text stands out */
    background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(/portfolio/static/media/home_image.94ba2a3f.jpg);
    background-size: cover;
    background-position: center;
    padding: 50px 0;
    color: white;
    text-align: center;
}

.home-header h1 {
    color: #ffffff;
    font-size: 3.5em;
    text-shadow: 2px 2px 10px rgba(0, 0, 0, 0.7); /* Slightly deeper shadow for better legibility */
    -webkit-animation: fadeIn 2s;
            animation: fadeIn 2s;
}

.main-name {
    color: #ffffff;
    -webkit-animation: glowPulse 5s infinite;
            animation: glowPulse 5s infinite;
}

@-webkit-keyframes glowPulse {
    0%, 100% { text-shadow: 0 0 10px #027498; } /* A more vibrant blue */
    50% { text-shadow: 0 0 25px #027498; } /* Stronger glow for visibility */
}

@keyframes glowPulse {
    0%, 100% { text-shadow: 0 0 10px #027498; } /* A more vibrant blue */
    50% { text-shadow: 0 0 25px #027498; } /* Stronger glow for visibility */
}

.Typewriter__wrapper {
    font-size: 2.8em;
    color: #f5f5f5;
    font-weight: 600;
    -webkit-animation: fadeIn 2s;
            animation: fadeIn 2s;
}

.Typewriter__cursor {
    font-size: 2.4em;
    color: #f5f5f5;
}

.about {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 50px 0;
    background: #f5f5f5;
}

.about-card {
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    border-radius: 5px;
}

.about-card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.card-header .card-body {
    margin: auto;
    display: flex;
    justify-content: center;
    -webkit-animation: fadeIn 2s;
            animation: fadeIn 2s;
}

@-webkit-keyframes fadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
}

@keyframes fadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
}

.wrapper {
    margin: 2rem;
    display: grid;
    grid-gap: 4rem;
    gap: 4rem;
    grid-template-columns: 1fr;
    justify-content: center;
    grid-template-columns: repeat(auto-fit, minmax(20rem, 25rem));
}

.card {
    background-color: #ecf0f1 !important;
    box-shadow: 0 0.1em 0.2em 0 rgba(0, 0, 0, 0.25), 0 0.1em 0.6em 0 rgba(0, 0, 0, 0.15) !important;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    transition: 0.2s ease;
    border: none;
    z-index: 1;
    position: relative;
    top: 0;
    text-align: left;
    border-radius: 10px;
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.card:hover {
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
}

.card__img {
    width: 100%;
    object-fit: cover;
    background-size: 105% auto;
    background-position: center;
    background-repeat: no-repeat;
    transition: background-size 0.6s ease;
    height: 11.8rem;
    border-radius: 10px 10px 0 0;
}

.card:hover .card__img {
    background-size: 100% auto;
}

.card__body {
    margin: 2rem 1rem 2rem 1rem;
    flex-grow: 1;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.card__title {
    line-height: 1.8rem;
    margin-bottom: 0.8rem;
    text-align: center;
    color: #2a5657;
    font-weight: bold;
}

.card__description {
    line-height: 1.5rem;
    padding-bottom: 5px;
    color: #2a5657;
}

@media screen and (max-width: 499px) {
    .card__img {
        height: 9rem;
    }
}

.card__line {
    border-top:1px dotted #000;
    background:#2a5657;
    width: 90%;
    margin: auto;
    position: absolute;
    bottom: 2.7rem;
}

.card__btn {
    transition: 0.2s;
    color: #2a5657;
    padding-right: 1rem;
}

.card__btn:hover {
    color: #027498;
    -webkit-transform: scale(1.2);
            transform: scale(1.2)
}

.card__footer {
    display: inline;
    padding-right: 20px;
    padding-left: 0;
    list-style: "-";
    justify-content: space-between;
    position: absolute;
    bottom: 0;
}

.card__footer li {
    display: inline-flex;
}
.skill-icon {
    bottom: 0 !important;
    color: #f5f5f5;
    list-style-type: none;
    horiz-align: center;
    vertical-align: center;
    align-items: center;
    display: inline-block;
    flex-direction: row;
    justify-content: space-around;
    padding: 2rem;
    text-align: center;
    background-color: #488e90;
    margin: 2.5rem;
    border-radius: 25px;
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.skill-icon:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.sec-skill-icon {
    font-size: 1em;
    text-align: center;
}

.skills-div {
    align-items: center;
    text-align: center;
}


.main-skills-h1,
.complementary-skills-h1 {
    width: 100%;
    overflow: hidden;
    vertical-align:middle;
    color: #c7dee0;
    background-color: #027498;
    padding: 5px;
}

.skill-icons {
    padding: 10px 0 10px 0;
}

.skill-icon-text {
    margin: 0;
    margin-top: 0.6rem;
    color: #f5f5f5;
}
.footer {
    background-color: #2a5657;
    width: 100%;
    position: absolute;
    bottom: 0 !important;
    justify-content: space-between;
    margin-top: 30px;
}

.footer-body {
    text-align: center !important;
    bottom: 0;
    overflow: hidden;
    position: relative;
    color: #f5f5f5;
    padding: 20px 0;
}

.social-icons {
    display: flex;
    color: #f5f5f5;
    margin: 0 auto;
    justify-content: center;
    align-items: center;
    max-width: 400px;
}

.social-icon {
    padding: 1rem;
    transition: color 0.3s ease-in-out, -webkit-transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out, color 0.3s ease-in-out;
    transition: transform 0.5s ease-in-out, color 0.3s ease-in-out, -webkit-transform 0.5s ease-in-out;
}

.social-icon:hover {
    color: #027498 !important;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

@media screen and (max-width: 499px) {
    .social-icons svg {
        height: 30px;
    }
}
.App {
  position: relative;
  min-height: 100vh;
  padding-bottom: 6rem;
}
