body {
    background: linear-gradient(135deg, #e6e6f0, #f5f5f5); /* Soft gradient to match the light theme */
    font-family: 'Roboto', sans-serif; /* Keeping it simple and professional */
}

.home-header {
    /* Using a dark overlay to ensure the text stands out */
    background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('home_image.jpg');
    background-size: cover;
    background-position: center;
    padding: 50px 0;
    color: white;
    text-align: center;
}

.home-header h1 {
    color: #ffffff;
    font-size: 3.5em;
    text-shadow: 2px 2px 10px rgba(0, 0, 0, 0.7); /* Slightly deeper shadow for better legibility */
    animation: fadeIn 2s;
}

.main-name {
    color: #ffffff;
    animation: glowPulse 5s infinite;
}

@keyframes glowPulse {
    0%, 100% { text-shadow: 0 0 10px #027498; } /* A more vibrant blue */
    50% { text-shadow: 0 0 25px #027498; } /* Stronger glow for visibility */
}

.Typewriter__wrapper {
    font-size: 2.8em;
    color: #f5f5f5;
    font-weight: 600;
    animation: fadeIn 2s;
}

.Typewriter__cursor {
    font-size: 2.4em;
    color: #f5f5f5;
}

.about {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 50px 0;
    background: #f5f5f5;
}

.about-card {
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    border-radius: 5px;
}

.about-card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.card-header .card-body {
    margin: auto;
    display: flex;
    justify-content: center;
    animation: fadeIn 2s;
}

@keyframes fadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
}
